import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class VendorLayoutService {

  readonly pageTitle = new BehaviorSubject('');

  constructor() {
    this.setPageTitle();
  }

  setPageTitle(value = null) {
    if (value === null) {
      value = 'Dashboard';
    }
    this.pageTitle.next(value);
  }

  getPageTitle() {
    return this.pageTitle.value;
  }

}
