import { Component, OnInit } from '@angular/core';
import { ClientLayoutService } from './client-layout.service';
import { UserService } from 'src/app/services/user.service';

declare interface RouteInfo {
  path: string;
  title: string;
  icon: string;
  class: string;
}

@Component({
  selector: 'app-client-layout',
  templateUrl: './client-layout.component.html',
  styleUrls: ['./client-layout.component.css']
})
export class ClientLayoutComponent implements OnInit {


  layoutRoutes: RouteInfo[] = [
    { path: '/client', title: 'Dashboard', icon: 'ni ni-tv-2', class: '' },
    { path: '/client/views', title: 'Post Views', icon: 'fa fa-eye', class: '' },
    { path: '/client/comments', title: 'Comments', icon: 'fa fa-comments', class: '' },
    { path: '/client/referred', title: 'Referred', icon: 'fa fa-eye', class: '' },
    { path: '/client/request', title: 'Requests', icon: 'fa fa-credit-card', class: '' },
  ];

  layoutRoutes2: RouteInfo[] = [
    { path: '/client/profile', title: 'Profile', icon: 'ni ni-single-02', class: '' },
  ];

  constructor(
    public clientLayoutService: ClientLayoutService,
    private userService: UserService
  ) {
    this.getClient();
  }

  ngOnInit(): void {
  }

  getClient() {
    this.userService.getClient(this.userService.current.value.id).subscribe(
      data => {
        this.clientLayoutService.client.next(data.data);
      }
    );
  }

}
