import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';

import { AdminLayoutComponent } from './layouts/admin-layout/admin-layout.component';
import { AuthLayoutComponent } from './layouts/auth-layout/auth-layout.component';
import { AuthGuard } from './guard/auth.guard';
import { VendorLayoutComponent } from './layouts/vendor-layout/vendor-layout.component';
import { ClientLayoutComponent } from './layouts/client-layout/client-layout.component';

const routes: Routes = [
  {
    path: 'admin',
    component: AdminLayoutComponent,
    canActivate: [AuthGuard],
    data: { role: 'admin' },
    children: [
      {
        path: '',
        loadChildren: './layouts/admin-layout/admin-layout.module#AdminLayoutModule'
      }
    ]
  }, {
    path: 'client',
    component: ClientLayoutComponent,
    canActivate: [AuthGuard],
    data: { role: 'client' },
    children: [
      {
        path: '',
        loadChildren: './layouts/client-layout/client-layout.module#ClientLayoutModule'
      }
    ]
  }, {
    path: 'vendor',
    component: VendorLayoutComponent,
    canActivate: [AuthGuard],
    data: { role: 'vendor' },
    children: [
      {
        path: '',
        loadChildren: './layouts/vendor-layout/vendor-layout.module#VendorLayoutModule'
      }
    ]
  }, {
    path: '',
    canActivate: [AuthGuard],
    data: { role: 'free' },
    component: AuthLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: './layouts/auth-layout/auth-layout.module#AuthLayoutModule'
      }
    ]
  }, {
    path: '**',
    redirectTo: 'login'
  }
];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes)
  ],
  exports: [
  ],
})
export class AppRoutingModule { }
