import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { UserService } from '../services/user.service';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {

  constructor(
    private userServices: UserService,
    private router: Router
  ) {

  }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return this.userServices.auth().pipe(map(data => {
      if (data.status === 'success') {
        const user = data.data;
        this.userServices.setUser(user);
        if (next.data.role === 'client') {
          return user.client !== null ? true : false;
        }
        if (next.data.role === 'admin') {
          return user.admin !== null ? true : false;
        }
        if (next.data.role === 'vendor') {
          return user.vendor !== null ? true : false;
        }
        if (next.data.role === 'free') {
          return true;
        }
      } else {
        if (next.data.role === 'free') {
          this.userServices.setUser({});
          return true;
        }
      }
      this.router.navigate(['/login']);
      return false;
    }));
  }

}
