import { Component, OnInit, ElementRef, Input, SimpleChanges, OnChanges } from '@angular/core';
import { ROUTES } from '../sidebar/sidebar.component';
import { Location, LocationStrategy, PathLocationStrategy } from '@angular/common';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit, OnChanges {

  @Input() pageTitle;

  @Input() dropDownItems;
  user;

  constructor(
    private userService: UserService
  ) {
  }

  ngOnInit() {
    this.userService.current.asObservable().subscribe(
      data => {
        this.user = data;
      }
    );

  }

  getTitle() {
    return this.pageTitle.value || 'DashBoard';
  }

  ngOnChanges(changes: SimpleChanges) {
    if (!changes.pageTitle.firstChange) {
      this.pageTitle = changes.pageTitle.currentValue;
    }
  }

  logout() {
    this.userService.logout();
  }

}
