import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ClientLayoutService {

  readonly pageTitle = new BehaviorSubject('');
  emptyClient: any;
  readonly client = new BehaviorSubject<any>(this.emptyClient);

  constructor() {
    this.setPageTitle();
  }

  setPageTitle(value = null) {
    if (value === null) {
      value = 'Dashboard';
    }
    this.pageTitle.next(value);
  }

  getPageTitle() {
    return this.pageTitle.value;
  }

}
