import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';
import { GeneralReturnType } from '../class/general';
import { Router } from '@angular/router';
import * as CryptoJS from 'crypto-js';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  readonly type = new BehaviorSubject<any>('');

  readonly current = new BehaviorSubject<any>({
    email: '',
    first_name: '',
    last_name: '',
    id: 0,
    image_url: '',
    phone: ''
  });

  constructor(
    private http: HttpClient,
    private router: Router,
  ) {
    this.setUser();
  }

  logout() {
    localStorage.removeItem('user');
    this.setUser({});
    this.logoutUser().subscribe((data) => {
      this.router.navigate(['/login']);
    });
  }

  logoutUser(): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/logout`);
  }

  setUser(data = null) {
    let type = '';
    if (data === null) {
      data = JSON.parse(localStorage.getItem('user'));
    } else {
      localStorage.setItem('user', JSON.stringify(data));
    }
    let user = {};
    switch (true) {
      case (data !== null && data.admin !== null && 'admin' in data):
        user = data.admin;
        type = 'admin';
        break;
      case (data !== null && data.client !== null && 'client' in data):
        user = data.client;
        type = 'client';
        break;
      case (data !== null && data.vendor !== null && 'vendor' in data):
        user = data.vendor;
        type = 'vendor';
        break;
      default:
        type = '';
        user = {
          email: '',
          first_name: '',
          last_name: '',
          id: 0,
          image_url: '',
          phone: ''
        };
    }

    this.current.next(user);
    this.type.next(type);
  }

  decryptEmail(id) {
    console.log(id);
    return CryptoJS.AES.decrypt(id, 'secret key 123').toString(CryptoJS.enc.Utf8);
  }

  encryptEmail(id) {
    return CryptoJS.AES.encrypt(id, 'secret key 123').toString();
  }

  auth(): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/auth`);
  }

  login(data): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/login`, data);
  }

  // Administrator
  createAdministrator(data): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/admin`, data);
  }

  getAdministrator(id): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/admin/${id}`);
  }

  toggleUserBlockState(id): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/user/${id}`);
  }

  getAdministrators(): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/admin`);
  }

  updateAdministrator(id, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/admin/${id}`, data);
  }

  updateAdministratorPassword(id, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/admin/${id}/password`, data);
  }

  updateAdministratorAvatar(id, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/admin/${id}/image`, data);
  }


  // Administrator Roles
  createAdministratorRole(data): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/admin_role`, data);
  }

  getAdministratorRole(id): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/admin_role/${id}`);
  }

  getAdministratorRoles(): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/admin_role`);
  }

  // Vendors
  createVendor(data): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/vendor`, data);
  }

  getVendor(id): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/vendor/${id}`);
  }

  getVendors(): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/vendor`);
  }

  updateVendor(id, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/vendor/${id}`, data);
  }

  updateVendorPassword(id, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/vendor/${id}/password`, data);
  }

  updateVendorAvatar(id, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/vendor/${id}/image`, data);
  }

  // Client
  createClient(data): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/client`, data);
  }

  getClient(id): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/client/${id}`);
  }

  getClients(): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/client`);
  }

  getReferredClients(id): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/client/${id}/referred`);
  }

  updateClient(id, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/client/${id}`, data);
  }

  updateClientPassword(id, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/client/${id}/password`, data);
  }

  updateClientAvatar(id, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/client/${id}/image`, data);
  }

  // Pins
  generatePins(data): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/pin`, data);
  }

  getPins(): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/pin`);
  }

  getVendorPins(id): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/vendor/${id}/pins`);
  }

  usePin(data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/pin`, data);
  }

}
