import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GeneralReturnType } from '../class/general';

@Injectable({
  providedIn: 'root'
})
export class SettingService {

  constructor(
    private http: HttpClient
  ) { }

  getSettings(): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/setting`);
  }

  updateSettings(data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/setting`, data);
  }

  createCategory(data):  Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/category`, data);
  }

  getCategories():  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/category`);
  }

  getCategery(id):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/category/${id}`);
  }

  deleteCategery(id):  Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/category/${id}`);
  }

  updateCategory(data):  Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/category`, data);
  }

  getCategeryPosts(id):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/category/${id}/posts`);
  }

  createPost(data):  Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/post`, data);
  }

  getPosts():  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/post`);
  }

  getPost(id):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/post/${id}`);
  }

  UpdatePost(id, data):  Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/post/${id}`, data);
  }

  deletePost(id):  Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/post/${id}`);
  }

  createComment(data):  Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/comment`, data);
  }

  getComments():  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/comment`);
  }

  getComment(id):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/comment/${id}`);
  }

  getPostComments(id):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/post/${id}/comments`);
  }

  getClientComments(id):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/client/${id}/comments`);
  }

  deleteComment(id):  Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/comment/${id}`);
  }

  validComment(id):  Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/comment/${id}`, {});
  }

  getAllPostViews():  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/post_views`);
  }

  getClientPostViews(id):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/client/${id}/post_views`);
  }

  getPostViews(id):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/post/${id}/post_views`);
  }

  makeRequest(data, type):  Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/request/${type}`, data);
  }

  getRequests():  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/request`);
  }

  updateRequest(id, type):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/request/${id}/${type}`);
  }

  getConfirmedRequests():  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/request/confirmed`);
  }

  getUnConfirmedRequests():  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/request/unconfirmed`);
  }

  getClientRequests(id):  Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/client/${id}/request`);
  }

  // UpdatePost(id, data):  Observable<GeneralReturnType> {
  //   return this.http.put<GeneralReturnType>(`/api/post/${id}`, data);
  // }

  // deletePost(id):  Observable<GeneralReturnType> {
  //   return this.http.delete<GeneralReturnType>(`/api/post/${id}`);
  // }

}
